.loyalty {

  @include media-breakpoint-down(lg) {
    font-size: rem(18);
    line-height: rem(24);
  }
  @include media-breakpoint-down(sm) {
    font-size: rem(14);
    line-height: rem(18);
  }
  p {
    margin-bottom: 22px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}
